import React from 'react';
import { useNavigate } from 'react-router-dom';
import { usePopularChains } from '../hooks/usePopularChains';
import LoadingState from './LoadingState';
import ErrorState from './ErrorState';
import EmptyState from './EmptyState';
import { getStatusMessages } from '../config/constants';
import ImageWithFallback from './ImageWithFallback';
import useTranslation from '../hooks/useTranslation';
import { getLocalizedValue } from '../config/languageConfig';
import { Chain } from '../types/chain';

const PopularChains: React.FC = () => {
  const { chains, loading, error, refetch } = usePopularChains();
  const navigate = useNavigate();
  const { t, language } = useTranslation();
  
  //    
  const STATUS_MESSAGES = getStatusMessages();
  
  //   
  const handleChainClick = (chainId: string, chain: Chain) => {
    const localizedName = getLocalizedChainName(chain);
    navigate(`/chains/${chainId}/${encodeURIComponent(localizedName)}`);
  };
  
  //    
  const getLocalizedChainName = (chain: Chain) => {
    //     
    switch(language) {
      case 'ko': 
        return chain.nameKo || chain.nameEn || chain.name;
      case 'ja':
        return chain.nameJa || chain.nameEn || chain.name;
      case 'zh-CN':
        return chain.nameZhCn || chain.nameEn || chain.name;
      case 'zh-TW':
        return chain.nameZhTw || chain.nameEn || chain.name;
      case 'en':
      default:
        return chain.nameEn || chain.name;
    }
  };

  //   
  if (loading) {
    return (
      <div className="p-4">
        <h2 className="text-xl font-semibold mb-4 text-left">{t('home.popularChains')}</h2>
        <LoadingState height="h-20" message={t('status.loading.chains')} />
      </div>
    );
  }

  //   
  if (error) {
    return (
      <div className="p-4">
        <h2 className="text-xl font-semibold mb-4 text-left">{t('home.popularChains')}</h2>
        <ErrorState height="h-20" message={error} onRetry={refetch} />
      </div>
    );
  }

  //   
  if (chains.length === 0) {
    return (
      <div className="p-4">
        <h2 className="text-xl font-semibold mb-4 text-left">{t('home.popularChains')}</h2>
        <EmptyState 
          message={t('status.empty.chains')} 
        />
      </div>
    );
  }

  return (
    <div className="p-4">
      <h2 className="text-xl font-semibold mb-4 text-left">{t('home.popularChains')}</h2>
      <div className="relative">
        <div className="flex overflow-x-auto scrollbar-hide pb-2 space-x-4" style={{ scrollbarWidth: 'none', msOverflowStyle: 'none' }}>
          {chains.map((chain) => (
            <div 
              key={chain.id} 
              className="flex flex-col items-center min-w-[80px] pb-3 cursor-pointer"
              onClick={() => handleChainClick(chain.id, chain)}
            >
              <div className="w-16 h-16 p-0 flex items-center justify-center mb-2">
                <ImageWithFallback 
                  src={chain.imageUrl} 
                  alt={getLocalizedChainName(chain)} 
                  className="w-14 h-14 rounded-full object-cover shadow-md"
                  style={{ display: 'block' }}
                  fallback="https://delivereee.github.io/home/images/chains/default-logo.png"
                />
              </div>
              <span className="text-sm text-center">{getLocalizedChainName(chain)}</span>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default PopularChains; 