/**
 * OpenStreetMap Nominatim API    
 */
export const reverseGeocode = async (lng: number, lat: number): Promise<string | null> => {
  try {
    // console.log(` : =${lng}, =${lat}`);
    
    // OpenStreetMap Nominatim API 
    const url = `https://nominatim.openstreetmap.org/reverse?format=json&lat=${lat}&lon=${lng}&zoom=18&addressdetails=1`;
    // console.log('API  URL:', url);
    
    const response = await fetch(url, {
      headers: {
        'Accept-Language': 'en,en-US;q=0.9,ko;q=0.8',
        'User-Agent': 'Delivereee App (https://delivereee.github.io)'
      }
    });
    
    // console.log('API  :', response.status, response.statusText);
    
    if (!response.ok) {
      console.error(' API  :', response.status, response.statusText);
      return null;
    }
    
    const data = await response.json();
    // console.log('Nominatim API  :', data);
    
    // Nominatim API   
    if (data && data.address) {
      const addr = data.address;
      let formattedAddress = '';
      
      //   
      if (addr.country_code === 'us') {
        //      ( ->  ->  -> )
        const parts = [];
        
        //   (, )
        if (addr.house_number) parts.push(addr.house_number);
        if (addr.road) parts.push(addr.road);
        
        // /
        if (addr.suburb) parts.push(addr.suburb);
        if (addr.city || addr.town || addr.village) {
          const city = addr.city || addr.town || addr.village;
          parts.push(city);
        }
        
        // /
        if (addr.state) parts.push(addr.state);
        
        // 
        if (addr.postcode) parts.push(addr.postcode);
        
        // 
        if (addr.country) parts.push(addr.country);
        
        formattedAddress = parts.join(', ');
      } 
      //   
      else if (addr.country_code === 'kr') {
        //     (/ -> / -> // -> )
        const parts = [];
        
        // 
        if (addr.country) parts.push(addr.country);
        
        // /
        if (addr.state) parts.push(addr.state);
        
        // //
        if (addr.county) parts.push(addr.county);
        if (addr.city) parts.push(addr.city);
        
        // //
        if (addr.suburb) parts.push(addr.suburb);
        if (addr.neighbourhood) parts.push(addr.neighbourhood);
        
        //   ()
        if (addr.road) parts.push(addr.road);
        if (addr.house_number) parts.push(addr.house_number);
        
        //   
        
        formattedAddress = parts.join(' ');
      }
      //   ( )
      else {
        const parts = [];
        
        //   (, )
        if (addr.house_number) parts.push(addr.house_number);
        if (addr.road) parts.push(addr.road);
        
        // /
        if (addr.suburb) parts.push(addr.suburb);
        if (addr.city || addr.town || addr.village) {
          parts.push(addr.city || addr.town || addr.village);
        }
        
        // /  
        if (addr.state) parts.push(addr.state);
        if (addr.country) parts.push(addr.country);
        
        formattedAddress = parts.join(', ');
      }
      
      // console.log(' :', formattedAddress);
      return formattedAddress;
    }
    
    return null;
  } catch (error) {
    console.error(' :', error);
    
    // IP     - ->     
    // console.log('IP    ...');
    try {
      const ipLocation = await getIpBasedLocation();
      if (ipLocation) {
        return `Near ${ipLocation.city || 'current location'}, ${ipLocation.region || ''}, ${ipLocation.country || ''}`;
      }
    } catch (ipError) {
      console.error('IP    :', ipError);
    }
    
    return null;
  }
};

/**
 *  Geolocation API     
 */
export const getCurrentPosition = (): Promise<{ lat: number; lng: number }> => {
  return new Promise((resolve, reject) => {
    if (!navigator.geolocation) {
      reject(new Error('Geolocation is not supported by this browser.'));
      return;
    }
    
    navigator.geolocation.getCurrentPosition(
      (position) => {
        const location = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        };
        // console.log('    :', location);
        resolve(location);
      },
      (error) => {
        console.error('    :', error);
        reject(error);
      },
      { enableHighAccuracy: true, timeout: 10000, maximumAge: 0 }
    );
  });
};

// : IP      (Geolocation  )
export const getIpBasedLocation = async (): Promise<{ lat: number; lng: number; city?: string; region?: string; country?: string } | null> => {
  try {
    const response = await fetch('https://ipapi.co/json/');
    
    if (!response.ok) {
      throw new Error(`IP    API : ${response.status}`);
    }
    
    const data = await response.json();
    
    if (data.latitude && data.longitude) {
      return {
        lat: data.latitude,
        lng: data.longitude,
        city: data.city,
        region: data.region,
        country: data.country_name
      };
    }
    
    return null;
  } catch (error) {
    console.error('IP     :', error);
    return null;
  }
}; 