import React, { useMemo, useEffect, useState } from 'react';
import { useParams, useLocation } from 'react-router-dom';
import BackHeader from '../components/BackHeader';
import RestaurantItem from '../components/RestaurantItem';
import LoadingState from '../components/LoadingState';
import ErrorState from '../components/ErrorState';
import EmptyState from '../components/EmptyState';
import NavigationBar from '../components/NavigationBar';
import { useRestaurants } from '../hooks/useRestaurants';
import { getCategories } from '../api/categoryService';
import { getStatusMessages } from '../config/constants';
import { Category } from '../types/category';
import { getCurrentLanguage } from '../config/languageConfig';
import useTranslation from '../hooks/useTranslation';
import { SupportedCurrency, getCurrencyFromLanguage } from '../utils/currencyUtils';

const CategoryDetail: React.FC = () => {
  const { categoryId, categoryName, chainName, chainId } = useParams<{ 
    categoryId?: string, 
    categoryName?: string,
    chainId?: string,
    chainName?: string
  }>();
  const location = useLocation();
  const [allCategories, setAllCategories] = useState<Category[]>([]);
  const [selectedCategory, setSelectedCategory] = useState<Category | null>(null);
  const { t } = useTranslation();
  const [currentLang, setCurrentLang] = useState(getCurrentLanguage());
  const [selectedCurrency, setSelectedCurrency] = useState<SupportedCurrency>(
    getCurrencyFromLanguage(currentLang)
  );
  
  //       
  useEffect(() => {
    const handleLanguageChange = () => {
      const newLang = getCurrentLanguage();
      setCurrentLang(newLang);
      setSelectedCurrency(getCurrencyFromLanguage(newLang));
    };
    
    //     
    window.addEventListener('language-changed', handleLanguageChange);
    
    //      
    return () => {
      window.removeEventListener('language-changed', handleLanguageChange);
    };
  }, []);
  
  //    
  const STATUS_MESSAGES = getStatusMessages();
  
  // API   
  useEffect(() => {
    const fetchCategoriesData = async () => {
      try {
        const categories = await getCategories();
        if (categories.length > 0) {
          setAllCategories(categories);
          
          // categoryId   
          if (categoryId) {
            const category = categories.find(cat => cat.id === categoryId);
            if (category) {
              setSelectedCategory(category);
            }
          }
        }
      } catch (error) {
        console.error('   :', error);
      }
    };
    
    fetchCategoriesData();
  }, [categoryId]);
  
  //     
  const isAllRestaurants = location.pathname === '/restaurants' || location.pathname === '/browse';
  
  //   
  const searchParams = useMemo(() => {
    if (isAllRestaurants) {
      return { allRestaurants: true };
    }
    
    if (chainId && chainName) {
      return { franchiseId: chainId };
    }
    
    //  ID   -  
    if (categoryId) {
      return { categoryId };
    }
    
    //     - 
    if (selectedCategory) {
      return { categoryId: selectedCategory.id };
    }
    
    //       
    if (categoryName) {
      //   ID  
      const category = allCategories.find(cat => 
        cat.nameKo === decodeURIComponent(categoryName) || cat.name === decodeURIComponent(categoryName)
      );
      
      if (category) {
        return { categoryId: category.id };
      }
      
      //      
      return { categoryName };
    }
    
    return {};
  }, [categoryId, categoryName, chainId, chainName, isAllRestaurants, selectedCategory, allCategories]);
  
  // API 
  const { restaurants, loading, error, refetch } = useRestaurants(searchParams);
  
  //    
  const getCategoryLocalizedName = (category: Category): string => {
    if (!category) return '';
    
    //     
    switch (currentLang) {
      case 'ko':
        return category.nameKo || category.nameEn || category.name;
      case 'ja':
        return category.nameJa || category.nameEn || category.name;
      case 'zh-CN':
        return category.nameZhCn || category.nameEn || category.name;
      case 'zh-TW':
        return category.nameZhTw || category.nameEn || category.name;
      case 'en':
      default:
        return category.nameEn || category.name;
    }
  };
  
  //   
  const displayTitle = useMemo((): string => {
    if (isAllRestaurants) {
      return t('browse.allRestaurants');
    }
    
    if (chainName) {
      //    (   )
      return decodeURIComponent(chainName);
    }
    
    //      
    if (selectedCategory) {
      return getCategoryLocalizedName(selectedCategory);
    }
    
    // ID  
    if (categoryId) {
      const category = allCategories.find(cat => cat.id === categoryId);
      if (category) {
        return getCategoryLocalizedName(category);
      }
    }
    
    //   ( )
    if (categoryName) {
      const decodedName = decodeURIComponent(categoryName);
      const category = allCategories.find(cat => 
        cat.nameKo === decodedName || cat.name === decodedName
      );
      if (category) {
        return getCategoryLocalizedName(category);
      }
      return decodedName;
    }
    
    return t('browse.restaurants');
  }, [categoryId, categoryName, chainName, isAllRestaurants, allCategories, selectedCategory, currentLang, t]);
  
  return (
    <div className="min-h-screen bg-gray-50">
      <BackHeader title={displayTitle} />
      
      <main className="p-4 pb-20">
        {/*  : // /    */}
        {loading ? (
          <LoadingState 
            message={STATUS_MESSAGES.loading.restaurants} 
            id={`category-${isAllRestaurants ? 'all-restaurants' : categoryId || 'unknown'}`}
          />
        ) : error ? (
          <ErrorState 
            message={t('status.error.restaurants')} 
            onRetry={refetch} 
            subtitle={t('error.connectionIssue')}
          />
        ) : restaurants.length === 0 ? (
          <EmptyState 
            message={STATUS_MESSAGES.empty.restaurants} 
            subtitle={t('empty.changeLocationOrCategory')}
            actionText={t('action.refresh')}
            onAction={refetch}
            hideIcon={true}
          />
        ) : (
          <div>
            <p className="text-gray-600 mb-4">
              {restaurants.length} {restaurants.length === 1 
                ? t('browse.restaurantSingular') 
                : t('browse.restaurantPlural')} {t('browse.available')}
            </p>
            {restaurants.map((restaurant) => (
              <RestaurantItem 
                key={restaurant.id} 
                restaurant={restaurant} 
                currency={selectedCurrency}
              />
            ))}
          </div>
        )}
      </main>
      
      {/*    */}
      <NavigationBar />
    </div>
  );
};

export default CategoryDetail; 