import apiClient from './config';
import { handleApiError, logApiResponse } from './utils';
import { Cart } from '../types/cart';

//   
export interface CreateCartRequest {
  addressId: string;
  deeFranchiseStoreId: string;
  restaurantName: string;
  languageCode: string;
  items: {
    franchiseMenuId: string;
    name: string;
    price: number;
    quantity: number;
    options?: {
      franchiseMenuOptionId: string;
      name: string;
      price: number;
    }[];
  }[];
}

//   
export interface CartResponse {
  id: string;
  memberId: string | null;
  addressId: string;
  orderNote: string | null;
  restaurant: {
    id: number;
    name: string;
  };
  items: {
    id?: number;
    menuId: number;
    name: string;
    price: number;
    quantity: number;
    options: {
      id?: number;
      optionId: number;
      name: string;
      price: number;
      optionItems: {
        id?: number;
        optionItemId: number;
        name: string;
        price: number;
      }[];
    }[];
    itemTotalPrice: number;
  }[];
  totalPrice: number;
  orderId: string | null;
  createdDate: string;
  updatedDate: string;
}

/**
 *    API   
 */
export const transformCartToRequest = (
  cart: Cart,
  addressId: string,
  languageCode: string = 'KO'
): CreateCartRequest => {
  return {
    addressId,
    deeFranchiseStoreId: String(cart.restaurantId),
    restaurantName: cart.restaurantName,
    languageCode,
    items: cart.items.map(item => ({
      franchiseMenuId: item.id,
      name: item.name,
      price: Math.round(item.price / 0.00071), // USD   (1 = 0.00071)
      quantity: item.quantity,
      options: item.options.map(option => ({
        franchiseMenuOptionId: option.id,
        name: option.name,
        price: Math.round(option.price / 0.00071), // USD  
      }))
    }))
  };
};

/**
 *   API
 * @param cartData    
 * @returns   
 */
export const createCart = async (cartData: CreateCartRequest): Promise<CartResponse> => {
  try {
    const response = await apiClient.post<CartResponse>('/api/carts', cartData);
    logApiResponse('createCart', response.data);
    return response.data;
  } catch (error) {
    throw handleApiError(error);
  }
};

/**
 *   API
 * @param cartId  ID
 * @returns  
 */
export const getCart = async (cartId: string): Promise<CartResponse> => {
  try {
    const response = await apiClient.get<CartResponse>(`/api/carts/${cartId}`);
    logApiResponse('getCart', response.data);
    return response.data;
  } catch (error) {
    throw handleApiError(error);
  }
}; 