import React, { useState, useEffect, useMemo } from "react";
import { useNavigate } from "react-router-dom";
import { useCart } from "../contexts/CartContext";
import BackHeader from "../components/BackHeader";
import { PayPalScriptProvider, PayPalButtons } from "@paypal/react-paypal-js";
import apiClient from "../api/config";
import {
  SupportedCurrency,
  formatPrice as formatPriceWithCurrency,
  EXCHANGE_RATES,
  getCurrencyFromLanguage,
} from "../utils/currencyUtils";
import { getCurrentLanguage, SupportedLanguage, LANGUAGE_CHANGE_EVENT } from '../config/languageConfig';
import useTranslation from '../hooks/useTranslation';
import { useAddress } from "../contexts/AddressContext";

const CheckoutPage: React.FC = () => {
  const navigate = useNavigate();
  const { cart, getTotalPrice, fetchOrderId } = useCart();
  const { address } = useAddress();
  const { t } = useTranslation();

  // Get currency from URL parameters
  const location = window.location;
  const urlParams = new URLSearchParams(location.search);
  const currencyParam = urlParams.get("currency");

  // Email state management
  const [email, setEmail] = useState("");
  const [emailValid, setEmailValid] = useState(false);

  // Delivery request state management
  const [deliveryRequest, setDeliveryRequest] = useState<string | null>(null);

  // Terms expansion state
  const [termsExpanded, setTermsExpanded] = useState(false);

  // Payment processing state
  const [isProcessing, setIsProcessing] = useState(false);
  const [paymentError, setPaymentError] = useState<string | null>(null);
  
  // OrderId 
  const [cartOrderId, setCartOrderId] = useState<string | null>(cart?.orderId || null);

  //   
  const [currentLanguage, setCurrentLanguage] = useState<SupportedLanguage>(getCurrentLanguage());

  // Currency selection state - use URL parameter if available, else based on current language
  const [selectedCurrency, setSelectedCurrency] = useState<SupportedCurrency>(
    (currencyParam as SupportedCurrency) || getCurrencyFromLanguage(currentLanguage),
  );

  //     
  useEffect(() => {
    const handleLanguageChange = (event: Event) => {
      const customEvent = event as CustomEvent;
      if (customEvent.detail && customEvent.detail.language) {
        const newLanguage = customEvent.detail.language as SupportedLanguage;
        setCurrentLanguage(newLanguage);
        // URL        
        if (!currencyParam) {
          setSelectedCurrency(getCurrencyFromLanguage(newLanguage));
        }
      }
    };

    window.addEventListener(LANGUAGE_CHANGE_EVENT, handleLanguageChange);
    return () => {
      window.removeEventListener(LANGUAGE_CHANGE_EVENT, handleLanguageChange);
    };
  }, [currencyParam]);

  //    orderId 
  useEffect(() => {
    const getOrderId = async () => {
      if (cart && address?.addressId && !cartOrderId) {
        try {
          const orderId = await fetchOrderId(address.addressId);
          if (orderId) {
            setCartOrderId(orderId);
          }
        } catch (error) {
          console.error("Error fetching order ID:", error);
        }
      }
    };

    getOrderId();
  }, [cart, address, cartOrderId, fetchOrderId]);

  // PayPal initial options
  const getPayPalClientId = () => {
    //  Client ID 
    if (process.env.REACT_APP_PAYPAL_CLIENT_ID) {
      return process.env.REACT_APP_PAYPAL_CLIENT_ID;
    }

    // GitHub Pages  
    const isGitHubPages =
      typeof window !== "undefined" &&
      window.location.hostname.includes("github.io");

    //   Client ID 
    return isGitHubPages
      ? "Af62TVjm80kRjCCyhj-EUa-fUjLoyWc8lbYyTvrVH8qdRetwc2Dk9sT4gxvCrKFBONJMB1fHnJYW0nYg" // 
      : "AWiQrkfr-umMAIIyeiLWY_Dgx3PMSazp9iUDofAQeNeIUnZo8sZuzDZPCdHJEqM9BYIE99g8DgzLI7a6"; // 
  };

  // Get PayPal supported currencies
  const getSupportedPayPalCurrencies = (): SupportedCurrency[] => {
    return ["USD", "JPY", "CNY", "TWD"];
  };

  const paypalOptions = {
    clientId: getPayPalClientId(),
    currency: selectedCurrency,
    intent: "capture",
    locale: "en_US",
  };

  //     
  if (process.env.NODE_ENV === "development") {
    console.log("  :", process.env.NODE_ENV);
    console.log(" Client ID:", getPayPalClientId());
    console.log("Selected Currency:", selectedCurrency);
    console.log("Current Language:", currentLanguage);
  }

  // Subtotal (cart items total) -      
  const subtotal = useMemo(() => getTotalPrice(), [getTotalPrice, currentLanguage]);
                       
  //    -   
  const DELIVERY_FEE = useMemo(() => {
    switch(currentLanguage) {
      case 'ko': return 3000;
      case 'ja': return 400;
      case 'zh-CN': return 20;
      case 'zh-TW': return 100;
      default: return 3;
    }
  }, [currentLanguage]);
                       
  const PROXY_FEE = useMemo(() => {
    switch(currentLanguage) {
      case 'ko': return 1000;
      case 'ja': return 150;
      case 'zh-CN': return 7;
      case 'zh-TW': return 30;
      default: return 1;
    }
  }, [currentLanguage]);

  // Total order amount -  
  const total = useMemo(() => subtotal + DELIVERY_FEE + PROXY_FEE, [subtotal, DELIVERY_FEE, PROXY_FEE]);

  // Price formatting with selected currency -    
  const formatPrice = (price: number) => {
    //    
    switch(selectedCurrency) {
      case 'USD':
        return `$${price.toFixed(2)}`;
      case 'JPY':
        return `${Math.round(price)}`;
      case 'KRW':
        return `${Math.round(price).toLocaleString()}`;
      case 'CNY':
        return `${price.toFixed(1)}`;
      case 'TWD':
        return `NT$${Math.round(price)}`;
      default:
        return formatPriceWithCurrency(price, selectedCurrency);
    }
  };

  // Email validation
  const validateEmail = (email: string) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  // Email input change handler
  const handleEmailChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newEmail = e.target.value;
    setEmail(newEmail);
    setEmailValid(validateEmail(newEmail));
  };

  // Currency selection handler
  const handleCurrencyChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    setSelectedCurrency(e.target.value as SupportedCurrency);
  };

  // Delivery request change handler
  const handleDeliveryRequestChange = (request: string) => {
    setDeliveryRequest(request === deliveryRequest ? null : request);
  };

  // Create PayPal order
  const createOrder = (data: any, actions: any) => {
    if (!emailValid || !cart) return;

    return actions.order.create({
      purchase_units: [
        {
          description: `Order from ${cart.restaurantName}`,
          amount: {
            currency_code: selectedCurrency,
            value: total.toFixed(2),
            breakdown: {
              item_total: {
                currency_code: selectedCurrency,
                value: subtotal.toFixed(2),
              },
              shipping: {
                currency_code: selectedCurrency,
                value: DELIVERY_FEE.toFixed(2),
              },
              handling: {
                currency_code: selectedCurrency,
                value: PROXY_FEE.toFixed(2),
              },
            },
          },
          items: cart.items.map((item) => {
            return {
              name: item.name,
              unit_amount: {
                currency_code: selectedCurrency,
                value: item.price.toFixed(2),
              },
              quantity: item.quantity,
            };
          }),
        },
      ],
      application_context: {
        shipping_preference: "NO_SHIPPING",
      },
    });
  };

  // PayPal payment approval handler
  const onApprove = async (data: any, actions: any) => {
    if (!cart) return;

    setIsProcessing(true);
    setPaymentError(null);

    try {
      const orderDetails = await actions.order.capture();
      console.log("PayPal payment successful:", orderDetails);

      // Get orderId from cart or from the API call
      const orderId = cartOrderId || cart.orderId;
      
      if (!orderId) {
        throw new Error("Order ID not found. Please try again.");
      }

      // PayPal order ID payment ID 
      const paymentId = data.orderID;

      // Extract PayPal transaction ID
      const transactionId =
        orderDetails.purchase_units[0]?.payments?.captures[0]?.id || "";

      // Prepare backend API request body
      const requestBody = {
        paymentMethod: "PAYPAL",
        paymentDetails: {
          paypalOrderId: paymentId,
          transactionId: transactionId,
          paymentStatus: "COMPLETED",
        },
        orderInfo: {
          email: email,
          items: cart.items.map((item) => ({
            id: item.id,
            name: item.name,
            quantity: item.quantity,
            price: item.price,
          })),
          subtotal: subtotal,
          deliveryFee: DELIVERY_FEE,
          proxyFee: PROXY_FEE,
          total: total,
          currency: selectedCurrency,
          paymentCurrency: selectedCurrency,
        },
        deliveryRequest: deliveryRequest || "",
      };

      let apiSuccess = false;
      let retryCount = 0;
      const MAX_RETRIES = 1;

      while (!apiSuccess && retryCount <= MAX_RETRIES) {
        try {
          // Call backend API using apiClient
          const response = await apiClient.post(
            `/api/orders/${orderId}/payment`,
            requestBody
          );

          if (response.status >= 200 && response.status < 300) {
            console.log("Order successfully processed:", response.data);
            apiSuccess = true;

            // Navigate to success page
            navigate("/order-success", {
              state: {
                orderId: orderId,
                paymentId: paymentId,
                currency: selectedCurrency,
              },
            });
          } else {
            console.error(`API call failed (attempt ${retryCount + 1}):`, response.statusText);
            retryCount++;
            if (retryCount <= MAX_RETRIES) {
              console.log(`Retrying API call (${retryCount}/${MAX_RETRIES})...`);
            }
          }
        } catch (error) {
          console.error(`API error (attempt ${retryCount + 1}):`, error);
          retryCount++;
          if (retryCount <= MAX_RETRIES) {
            console.log(`Retrying API call (${retryCount}/${MAX_RETRIES})...`);
          }
        }
      }

      //     
      if (!apiSuccess) {
        //      -   API 
        try {
          await apiClient.post(
            "https://api.brainbackdoor.com/api/notifications/slack/test/order",
            {
              message: ` API  . orderId: ${orderId}, paymentId: ${paymentId}, : ${formatPrice(total)}, : ${email}, : ${new Date().toISOString()}`
            }
          );
        } catch (slackError) {
          console.error("Failed to send error notification:", slackError);
        }

        //    
        setPaymentError(
          "      .  ."
        );
      }
    } catch (error) {
      console.error("PayPal payment error:", error);
      setPaymentError(
        "An error occurred during payment processing. Please try again."
      );
    } finally {
      setIsProcessing(false);
    }
  };

  // Redirect to cart if cart is empty
  if (!cart || cart.items.length === 0) {
    navigate("/cart");
    return null;
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <BackHeader title={t('cart.checkout') || "Checkout"} />

      <main className="px-4 py-4 pb-32">
        <h1 className="text-xl font-bold text-gray-800 mb-4 text-left">
          {t('cart.orderSummary')}
        </h1>

        {/* Order information section */}
        <div className="bg-gray-100 rounded-lg p-4 mb-4">
          {/* Restaurant information */}
          <h2 className="text-base text-gray-500 mb-3 text-left">
            {/*     */}
            {(() => {
              //      
              const restaurantNameField = `restaurantName${currentLanguage === 'en' ? 'En' : 
                                         currentLanguage === 'ja' ? 'Ja' : 
                                         currentLanguage === 'zh-CN' ? 'ZhCn' : 
                                         currentLanguage === 'zh-TW' ? 'ZhTw' : ''}` as keyof typeof cart;
              
              //      
              if (cart[restaurantNameField] !== undefined) {
                return String(cart[restaurantNameField]);
              }
              
              //    
              if (cart.restaurantNameEn !== undefined) {
                return String(cart.restaurantNameEn);
              }
              
              //   
              return cart.restaurantName;
            })()}
          </h2>

          {/* Menu list */}
          <div className="space-y-3 mb-2">
            {cart.items.map((item) => (
              <div key={item.id} className="flex justify-between">
                <div className="flex-1 flex flex-col">
                  <div className="flex items-start">
                    <span className="text-gray-600 mr-2">{item.quantity}</span>
                    <span className="text-gray-800">
                      {/*       ,     */}
                      {(() => {
                        //          
                        const nameField = `name${currentLanguage === 'en' ? 'En' : 
                                           currentLanguage === 'ja' ? 'Ja' : 
                                           currentLanguage === 'zh-CN' ? 'ZhCn' : 
                                           currentLanguage === 'zh-TW' ? 'ZhTw' : ''}` as keyof typeof item;
                        
                        return item[nameField] !== undefined ? String(item[nameField]) : item.name;
                      })()}
                    </span>
                  </div>
                  {/*     */}
                  {item.options.length > 0 &&
                    item.options.some((opt) => opt.optionItems.length > 0) && (
                      <div className="ml-6 mt-1 text-xs text-gray-500 text-left">
                        {item.options
                          .filter((option) => option.optionItems.length > 0)
                          .flatMap((option) => option.optionItems)
                          .map((optItem, index, allOptionItems) => (
                            <span key={optItem.id} className="inline">
                              {optItem.name}
                              {index < allOptionItems.length - 1 ? ", " : ""}
                            </span>
                          ))}
                      </div>
                    )}
                </div>
                <span className="text-gray-800 font-medium ml-4">
                  {formatPrice(
                    (() => {
                      //      
                      const priceField = `price${selectedCurrency.charAt(0).toUpperCase() + selectedCurrency.slice(1).toLowerCase()}` as keyof typeof item;
                      //     ,    
                      const itemPrice = item[priceField] !== undefined ? Number(item[priceField]) : item.price;
                      return itemPrice * item.quantity;
                    })()
                  )}
                </span>
              </div>
            ))}
          </div>
        </div>

        {/* Payment summary information */}
        <div className="bg-gray-100 rounded-lg p-4 mb-6">
          <div className="space-y-3 mb-3">
            <div className="flex justify-between items-center">
              <p className="text-gray-600">{t('cart.subtotal')}</p>
              <p className="font-medium text-gray-800">
                {formatPrice(subtotal)}
              </p>
            </div>

            <div className="flex justify-between items-center">
              <p className="text-gray-600">{t('cart.deliveryFee')}</p>
              <p className="font-medium text-gray-800">
                {formatPrice(DELIVERY_FEE)}
              </p>
            </div>

            <div className="flex justify-between items-center">
              <p className="text-gray-600">{t('cart.proxyFee')}</p>
              <p className="font-medium text-gray-800">
                {formatPrice(PROXY_FEE)}
              </p>
            </div>
          </div>

          <div className="border-t border-gray-300 pt-3 flex justify-between items-center">
            <p className="font-medium text-lg text-gray-900">{t('cart.total')}</p>
            <p className="font-semibold text-lg text-gray-900">
              {formatPrice(total)}
            </p>
          </div>
        </div>

        {/* Email input section */}
        <div className="mb-6">
          <label
            htmlFor="email"
            className="block text-gray-800 font-medium mb-2 text-left"
          >
            {t('checkout.email')} <span className="text-red-500">*</span>
          </label>
          <input
            type="email"
            id="email"
            value={email}
            onChange={handleEmailChange}
            placeholder={t('checkout.enterEmail')}
            className={`w-full p-3 border rounded-lg focus:outline-none focus:ring-2 ${
              email && !emailValid
                ? "border-red-500 focus:ring-red-200"
                : "border-gray-300 focus:ring-blue-200"
            }`}
          />
          {email && !emailValid && (
            <p className="mt-1 text-sm text-red-500 text-left">
              {t('checkout.invalidEmail')}
            </p>
          )}
          <p className="mt-1 text-sm text-gray-500 text-left">
            {t('checkout.confirmationEmail')}
          </p>
        </div>

        {/* Delivery requests section */}
        <div className="mb-6">
          <h2 className="text-xl font-bold text-gray-800 mb-3 text-left">
            {t('checkout.deliveryRequests')}
          </h2>
          <div className="grid grid-cols-2 gap-3">
            <button
              type="button"
              onClick={() =>
                handleDeliveryRequestChange(t('checkout.noRingBell'))
              }
              className={`p-3 border rounded-lg text-left ${
                deliveryRequest === t('checkout.noRingBell')
                  ? "border-red-500 bg-red-50"
                  : "border-gray-300 hover:border-gray-400"
              }`}
            >
              {t('checkout.noRingBell')}
            </button>

            <button
              type="button"
              onClick={() => handleDeliveryRequestChange(t('checkout.leaveAtDoor'))}
              className={`p-3 border rounded-lg text-left ${
                deliveryRequest === t('checkout.leaveAtDoor')
                  ? "border-red-500 bg-red-50"
                  : "border-gray-300 hover:border-gray-400"
              }`}
            >
              {t('checkout.leaveAtDoor')}
            </button>

            <button
              type="button"
              onClick={() =>
                handleDeliveryRequestChange(t('checkout.callIfProblem'))
              }
              className={`p-3 border rounded-lg text-left ${
                deliveryRequest === t('checkout.callIfProblem')
                  ? "border-red-500 bg-red-50"
                  : "border-gray-300 hover:border-gray-400"
              }`}
            >
              {t('checkout.callIfProblem')}
            </button>

            <button
              type="button"
              onClick={() =>
                handleDeliveryRequestChange(t('checkout.stayingAtRoom'))
              }
              className={`p-3 border rounded-lg text-left ${
                deliveryRequest === t('checkout.stayingAtRoom')
                  ? "border-red-500 bg-red-50"
                  : "border-gray-300 hover:border-gray-400"
              }`}
            >
              {t('checkout.stayingAtRoom')}
            </button>

            <button
              type="button"
              onClick={() => handleDeliveryRequestChange(t('checkout.addUtensils'))}
              className={`p-3 border rounded-lg text-left ${
                deliveryRequest === t('checkout.addUtensils')
                  ? "border-red-500 bg-red-50"
                  : "border-gray-300 hover:border-gray-400"
              }`}
            >
              {t('checkout.addUtensils')}
            </button>
          </div>
        </div>

        {/* Payment method section */}
        <div className="mb-6">
          <h2 className="text-xl font-bold text-gray-800 mb-3 text-left">
            {t('checkout.paymentMethod')}
          </h2>

          <div className="p-4 bg-gray-50 rounded-lg border border-gray-200 mb-4">
            <p className="text-gray-700">
              {t('checkout.paypalSecure')}
            </p>
            <p className="text-sm text-gray-500 mt-1">
              {t('checkout.paypalOptions')}
            </p>
            <p className="text-sm text-gray-500 mt-1">
              {t('checkout.currency')}: {selectedCurrency}
            </p>
          </div>

          {/* Display payment error if any */}
          {paymentError && (
            <div className="mt-2 p-3 bg-red-50 border border-red-200 rounded-lg text-red-700 text-sm mb-4">
              {paymentError}
            </div>
          )}

          {/* PayPal button section */}
          {emailValid ? (
            <div className="mt-4">
              <PayPalScriptProvider options={paypalOptions}>
                <PayPalButtons
                  style={{ layout: "vertical" }}
                  createOrder={createOrder}
                  onApprove={onApprove}
                  disabled={isProcessing}
                  onError={(err) => {
                    console.error("PayPal error:", err);
                    setPaymentError(
                      "An error occurred with PayPal. Please try again.",
                    );
                  }}
                />
              </PayPalScriptProvider>

              {isProcessing && (
                <div className="mt-3 text-center text-gray-600">
                  <p>Processing your payment. Please wait...</p>
                </div>
              )}
            </div>
          ) : (
            <div className="mt-4 p-4 bg-yellow-50 border border-yellow-100 rounded-lg text-center">
              <p className="text-yellow-700">
                Please enter a valid email address to access the PayPal payment
                button.
              </p>
            </div>
          )}
        </div>

        {/* Terms section */}
        <div className="mt-4 text-center text-gray-500 text-sm">
          <p className="mb-2">
            By proceeding with payment,
            <button
              type="button"
              className="text-gray-700 font-medium inline-flex items-center ml-1"
              onClick={() => setTermsExpanded(!termsExpanded)}
            >
              you agree to our Privacy Policy and Terms
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 24 24"
                fill="currentColor"
                className={`w-5 h-5 ml-1 transition-transform ${termsExpanded ? "rotate-180" : ""}`}
              >
                <path d="M7 10l5 5 5-5z" />
              </svg>
            </button>
          </p>

          {termsExpanded && (
            <div className="mt-3 text-left bg-gray-50 p-4 rounded-lg border border-gray-200 text-xs leading-relaxed">
              <h3 className="font-bold mb-2 text-gray-700">
                Privacy Policy and Terms of Service
              </h3>
              <p className="mb-2">
                <strong>1. Collection of Personal Information:</strong> We
                collect personal information such as your name, email address,
                phone number, and delivery address to process your order and
                provide you with a seamless food delivery experience.
              </p>
              <p className="mb-2">
                <strong>2. Use of Information:</strong> The information we
                collect is used to process your orders, manage your account,
                provide customer support, and improve our services.
              </p>
              <p className="mb-2">
                <strong>3. Payment Information:</strong> Your payment
                information is processed securely through our payment partners.
                We do not store your full credit card details on our servers.
              </p>
              <p className="mb-2">
                <strong>4. Refund Policy:</strong> Refunds are processed
                according to our refund policy, which may vary depending on the
                restaurant and the circumstances of the refund request.
              </p>
              <p className="mb-2">
                <strong>5. Delivery Terms:</strong> Delivery times are estimates
                and may vary depending on factors such as traffic, weather
                conditions, and restaurant preparation times.
              </p>
              <p className="mb-2">
                <strong>6. Multiple Currencies:</strong> While we display prices
                in multiple currencies for your convenience, the final charges
                may be subject to exchange rates at the time of processing.
              </p>
              <p>
                By proceeding with payment, you acknowledge that you have read
                and agree to these terms and conditions.
              </p>
            </div>
          )}
        </div>
      </main>
    </div>
  );
};

export default CheckoutPage;
