import React from 'react';
import { MenuSection as MenuSectionType } from '../types/menu';
import MenuItem from './MenuItem';
import { getLocalizedValue, getCurrentLanguage } from '../config/languageConfig';
import { SupportedCurrency, getCurrencyFromLanguage } from '../utils/currencyUtils';

interface RestaurantNames {
  name: string;
  nameEn: string | null;
  nameKo: string | null;
  nameJa: string | null;
  nameZhCn: string | null;
  nameZhTw: string | null;
}

interface MenuSectionProps {
  section: MenuSectionType;
  restaurantId: string;
  restaurantName: string;
  restaurantMultiNames?: RestaurantNames;
  currency?: SupportedCurrency;
}

const MenuSection: React.FC<MenuSectionProps> = ({ 
  section, 
  restaurantId, 
  restaurantName,
  restaurantMultiNames,
  currency
}) => {
  //      
  const currentLang = getCurrentLanguage();
  const effectiveCurrency = currency || getCurrencyFromLanguage(currentLang);
  
  const displayName = getLocalizedValue(section.name, {
    en: section.nameEn
  });
  
  return (
    <div className="mb-6 pb-4 border-b border-gray-200">
      {/*   */}
      <div className="mb-4">
        {/*    */}
        <h2 className="text-xl font-bold text-gray-800 text-left">{displayName}</h2>
      </div>
      
      {/*    */}
      <div>
        {section.menuItems.length === 0 ? (
          <p className="text-gray-500 italic">No items available in this section</p>
        ) : (
          <div>
            {section.menuItems.map((item, index) => (
              <div key={item.id} className={index !== section.menuItems.length - 1 ? "border-b border-gray-100" : ""}>
                <MenuItem 
                  menuItem={item} 
                  restaurantId={restaurantId}
                  restaurantName={restaurantName}
                  restaurantMultiNames={restaurantMultiNames}
                  currency={effectiveCurrency}
                />
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default MenuSection; 