//   
export type SupportedLanguage = 'en' | 'ko' | 'ja' | 'zh-CN' | 'zh-TW';

//   
export const DEFAULT_LANGUAGE: SupportedLanguage = 'en';

//     
export const LANGUAGE_CHANGE_EVENT = 'language-changed';

/**
 *      
 * @returns   
 */
export const detectBrowserLanguage = (): SupportedLanguage => {
  const browserLang = navigator.language.toLowerCase();
  
  //      
  if (browserLang.startsWith('ko')) return 'ko';
  if (browserLang.startsWith('ja')) return 'ja';
  if (browserLang.startsWith('zh-cn')) return 'zh-CN';
  if (browserLang.startsWith('zh-tw')) return 'zh-TW';
  if (browserLang.startsWith('zh')) {
    //        
    return 'zh-CN';
  }
  
  //  
  return 'en';
};

/**
 *   OS  
 * @param lang  
 * @returns    true (, ,  ,  )
 */
export const isSupportedOSLanguage = (lang: SupportedLanguage): boolean => {
  return ['ja', 'zh-CN', 'zh-TW'].includes(lang);
};

//    (:     )
//    (SSR  )
let currentLanguage: SupportedLanguage = DEFAULT_LANGUAGE;

//   
if (typeof window !== 'undefined' && typeof navigator !== 'undefined') {
  try {
    //      (     )
    const savedLanguage = localStorage.getItem('app_language') as SupportedLanguage | null;
    
    if (savedLanguage && ['en', 'ko', 'ja', 'zh-CN', 'zh-TW'].includes(savedLanguage)) {
      currentLanguage = savedLanguage;
    } else {
      //      
      const detectedLanguage = detectBrowserLanguage();
      
      //   :
      // 1. , , ,     
      // 2.       
      if (isSupportedOSLanguage(detectedLanguage)) {
        // , (/)   
        currentLanguage = detectedLanguage;
      } else {
        //       
        currentLanguage = 'en';
      }
      
      //   
      localStorage.setItem('app_language', currentLanguage);
    }
    
    console.log(` : ${currentLanguage} (${currentLanguage === detectBrowserLanguage() ? ' ' : ' '})`);
  } catch (error) {
    // localStorage        
    console.error('   :', error);
    currentLanguage = DEFAULT_LANGUAGE;
  }
}

/**
 *    
 * @returns   
 */
export const getCurrentLanguage = (): SupportedLanguage => {
  return currentLanguage;
};

/**
 *    
 * @returns      true
 */
export const isDeviceLanguage = (): boolean => {
  return currentLanguage === detectBrowserLanguage();
};

/**
 *   
 * @param language   
 */
export const setLanguage = (language: SupportedLanguage): void => {
  //       
  if (language === currentLanguage) {
    console.log(` ${language}   .`);
    return;
  }

  currentLanguage = language;
  
  //   
  if (typeof window !== 'undefined') {
    try {
      //    
      localStorage.setItem('app_language', language);
      
      //     -    
      const event = new CustomEvent(LANGUAGE_CHANGE_EVENT, { 
        detail: { 
          language,
          timestamp: Date.now()
        } 
      });
      window.dispatchEvent(event);
      
      console.log(` : ${language}`);
    } catch (error) {
      console.error('   :', error);
    }
  }
};

/**
 *       
 *        
 * 
 * @param defaultValue   
 * @param translations     (: { en: 'English value', ko: ' ' })
 * @returns      
 */
export const getLocalizedValue = (
  defaultValue: string,
  translations: Partial<Record<SupportedLanguage, string | null | undefined>>
): string => {
  //       
  if (!defaultValue && !translations) return '';
  
  // defaultValue    
  const safeDefaultValue = defaultValue || '';
  
  // translations      
  if (!translations || typeof translations !== 'object') {
    return safeDefaultValue;
  }
  
  //           
  const translatedValue = translations[currentLanguage];
  if (translatedValue) {
    return translatedValue;
  }
  
  //          
  if (translations.en && currentLanguage !== 'en') {
    return translations.en;
  }
  
  //     
  return safeDefaultValue;
}; 