import axios from 'axios';

//   API  URL 
const getApiBaseUrl = () => {
  // 1.    API URL  
  if (process.env.REACT_APP_API_BASE_URL) {
    console.log(`Using API URL from env var: ${process.env.REACT_APP_API_BASE_URL}`);
    return process.env.REACT_APP_API_BASE_URL;
  }
  
  // 2.  URL     (GitHub Pages  )
  const isGitHubPages = typeof window !== 'undefined' && window.location.hostname.includes('github.io');
  
  // 3. NODE_ENV   
  const isProdEnv = process.env.NODE_ENV === 'production';
  
  //   GitHub Pages    API URL 
  if (isProdEnv || isGitHubPages) {
    console.log('Detected production environment, using production API URL');
    return 'https://api.brainbackdoor.com';
  }
  
  //    URL 
  console.log('Using development API URL');
  return 'http://localhost:8084';
};

const API_BASE_URL = getApiBaseUrl();
const ENVIRONMENT = process.env.REACT_APP_ENV || process.env.NODE_ENV || 'development';
const isDevelopment = process.env.NODE_ENV === 'development';

// axios  
const apiClient = axios.create({
  baseURL: API_BASE_URL,
  headers: {
    'Content-Type': 'application/json',
  },
});

//    
console.log(`API Client configured with base URL: ${API_BASE_URL}`);
console.log(`Current environment: ${ENVIRONMENT}`);

//     
if (isDevelopment) {
  checkServerStatus();
}

/**
 * API   
 */
async function checkServerStatus() {
  try {
    const response = await axios.get(`${API_BASE_URL}/api/v1/stores?lat=37.4743358&lng=126.93812149`, { timeout: 5000 });
    console.log(' API   :', response.status);
    console.log('    :', Array.isArray(response.data) ? response.data.length : 'N/A');
    
    //  API    -    
    /* 
    try {
      const menuResponse = await axios.get(`${API_BASE_URL}/api/v1/stores/LZXaAODk/menus`, { timeout: 5000 });
      console.log('  API  :', menuResponse.status);
      console.log('    :', Array.isArray(menuResponse.data) ? menuResponse.data.length : 'N/A');
    } catch (menuError) {
      console.warn('  API  :', menuError);
    }
    */
  } catch (error) {
    console.error(' API   :', error);
  }
}

//   -   
apiClient.interceptors.request.use(
  (config) => {
    // /   API  
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

//   -   
apiClient.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    //   
    console.error('API Error:', error);
    return Promise.reject(error);
  }
);

export default apiClient; 