import apiClient from './config';
import { Category } from '../types/category';
import { getCurrentLanguage } from '../config/languageConfig';
import { buildQueryString } from './utils';
import { createCacheKey, getCacheData, setCacheData } from './cacheUtils';

/**
 *      
 * @param relativePath  
 * @returns   URL
 */
const getCategoryImageUrl = (relativePath: string): string => {
  if (process.env.NODE_ENV === 'development') {
    return `${process.env.PUBLIC_URL}${relativePath}`;
  }
  // GitHub Pages  
  return `https://delivereee.github.io/home${relativePath}`;
};

/**
 *   
 * @returns  
 */
export const getCategories = async (): Promise<Category[]> => {
  try {
    //    
    const currentLang = getCurrentLanguage();
    
    //   
    const endpoint = '/api/dee-categories';
    const params = { lang: currentLang };
    
    //   
    const cacheKey = createCacheKey(endpoint, params);
    
    //   
    const cachedData = getCacheData<Category[]>(cacheKey, currentLang);
    if (cachedData) {
      return cachedData;
    }
    
    //   
    const queryString = buildQueryString(params);
    
    // API    (  )
    const response = await apiClient.get(`${endpoint}${queryString}`);
    console.log(` : ${endpoint}${queryString}`);
    
    if (response.data && Array.isArray(response.data)) {
      // API   Category   
      const categories = response.data.map((item: any) => ({
        id: item.id,
        name: item.name, //   
        nameKo: item.nameKo,
        nameEn: item.nameEn,
        nameJa: item.nameJa,
        nameZhCn: item.nameZhCn,
        nameZhTw: item.nameZhTw,
        imageUrl: getCategoryImageUrl(item.imageUrl) //     
      }));
      
      //   
      setCacheData(cacheKey, categories, currentLang);
      
      return categories;
    }
    
    //       
    console.warn('API     .');
    return [];
  } catch (error) {
    console.error('API    :', error);
    // API      
    return [];
  }
};