import { useState, useEffect, useCallback } from 'react';
import { getRestaurantMenus } from '../api/menuService';
import { MenuSection } from '../types/menu';
import { getStatusMessages } from '../config/constants';
import { getCurrentLanguage } from '../config/languageConfig';

/**
 *      
 * @param restaurantId  ID
 */
export const useRestaurantMenus = (restaurantId: string | number) => {
  const [menuSections, setMenuSections] = useState<MenuSection[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  
  const fetchMenus = useCallback(async () => {
    if (!restaurantId) {
      setError('Restaurant ID is required');
      setLoading(false);
      return;
    }
    
    try {
      setLoading(true);
      setError(null);
      
      //   
      const currentLang = getCurrentLanguage();
      
      //   API  
      const data = await getRestaurantMenus(restaurantId, currentLang);
      
      if (Array.isArray(data) && data.length === 0) {
        console.warn(`No menu items found for restaurant ${restaurantId}`);
      }
      
      setMenuSections(data);
    } catch (err) {
      console.error('Error fetching restaurant menus:', err);
      const STATUS_MESSAGES = getStatusMessages();
      setError(STATUS_MESSAGES.error.menus || 'Failed to load menus. Please try again later.');
    } finally {
      setLoading(false);
    }
  }, [restaurantId]);
  
  //         
  useEffect(() => {
    const handleLanguageChange = () => {
      fetchMenus();
    };
    
    //     
    window.addEventListener('language-changed', handleLanguageChange);
    
    //      
    return () => {
      window.removeEventListener('language-changed', handleLanguageChange);
    };
  }, [fetchMenus]);
  
  useEffect(() => {
    fetchMenus();
  }, [fetchMenus]);
  
  return { menuSections, loading, error, refetch: fetchMenus };
}; 