import React, { createContext, useContext, useReducer, useEffect, ReactNode, useState, useCallback } from 'react';
import { Cart, CartItem } from '../types/cart';
import { getCurrentLanguage, LANGUAGE_CHANGE_EVENT, SupportedLanguage } from '../config/languageConfig';
import { getCurrencyFromLanguage, getPriceFieldByCurrency, SupportedCurrency, convertCurrency } from '../utils/currencyUtils';
import { createCart, getCart } from '../api/cartService';
import { transformCartToRequest } from '../api/cartService';

//      (  )
interface MinOrderAmount {
  amount: number;
  currency: SupportedCurrency;
}

interface RestaurantInfo {
  restaurantName: string;
  restaurantNameEn?: string;
  restaurantNameJa?: string;
  restaurantNameZhCn?: string;
  restaurantNameZhTw?: string;
  restaurantNameKo?: string;
}

//    
type CartAction = 
  | { type: 'ADD_ITEM'; payload: { restaurantId: string; restaurantInfo: RestaurantInfo; item: CartItem } }
  | { type: 'UPDATE_ITEM_QUANTITY'; payload: { itemId: string; quantity: number } }
  | { type: 'REMOVE_ITEM'; payload: { itemId: string } }
  | { type: 'CLEAR_CART' }
  | { type: 'REPLACE_CART'; payload: { restaurantId: string; restaurantInfo: RestaurantInfo; item: CartItem } }
  | { type: 'UPDATE_PRICES'; payload: { language: SupportedLanguage } }
  | { type: 'SET_ORDER_ID'; payload: { orderId: string } };

//    
interface CartContextType {
  cart: Cart | null;
  currentRestaurantId: string | null;
  setCurrentRestaurantId: (id: string | null) => void;
  showCartReplaceConfirm: boolean;
  pendingCartItem: { restaurantId: string; restaurantInfo: RestaurantInfo; item: CartItem } | null;
  canceledItemId: string | null;
  restaurantMinOrderAmount: MinOrderAmount | null;
  setRestaurantMinOrderAmount: (amount: number | null, currency?: SupportedCurrency) => void;
  addToCart: (restaurantId: string, restaurantInfo: RestaurantInfo, item: CartItem) => void;
  updateItemQuantity: (itemId: string, quantity: number) => void;
  removeItem: (itemId: string) => void;
  clearCart: () => void;
  getTotalPrice: () => number;
  getTotalItems: () => number;
  getMinOrderAmount: () => number | null;
  isDeliveryAvailable: (minOrderAmount: number | null) => boolean;
  getAmountToMinOrder: (minOrderAmount: number | null) => number;
  switchMessage: string | null;
  clearSwitchMessage: () => void;
  confirmReplaceCart: () => void;
  cancelReplaceCart: () => void;
  clearCanceledItem: () => void;
  updateCartPricesForLanguage: (language: SupportedLanguage) => void;
  fetchOrderId: (addressId: string) => Promise<string | null>;
}

//   
const initialCart: Cart | null = null;

//     
const loadCartFromStorage = (): Cart | null => {
  try {
    const cartData = localStorage.getItem('cart');
    if (!cartData) return null;
    
    const parsedCart = JSON.parse(cartData) as Cart;
    
    //    -    
    if (!parsedCart.restaurantId || !parsedCart.restaurantName || !Array.isArray(parsedCart.items)) {
      console.error('Invalid cart data in localStorage, missing required fields');
      localStorage.removeItem('cart'); //   
      return null;
    }
    
    //       (  )
    return parsedCart;
  } catch (error) {
    console.error('Error loading cart from localStorage:', error);
    return null;
  }
};

//      
const getPriceFieldByLanguage = (language: SupportedLanguage): string => {
  switch (language) {
    case 'ko': return 'priceKrw';
    case 'en': return 'priceUsd';
    case 'ja': return 'priceJpy';
    case 'zh-CN': return 'priceCny';
    case 'zh-TW': return 'priceTwd';
    default: return 'price';
  }
};

//        (API    )
const getMenuItemPriceByLanguage = (menuItem: any, language: SupportedLanguage): number => {
  const priceField = getPriceFieldByLanguage(language);
  return menuItem[priceField] !== undefined ? menuItem[priceField] : menuItem.price || 0;
};

//   
const cartReducer = (state: Cart | null, action: CartAction): Cart | null => {
  switch (action.type) {
    case 'ADD_ITEM': {
      const { restaurantId, restaurantInfo, item } = action.payload;
      
      //    -   
      if (!state) {
        console.log(`Creating new cart for restaurant: ${restaurantId}, ${restaurantInfo.restaurantName}`);
        //        
        return {
          id: `cart-${Date.now()}`, //  ID 
          restaurantId,
          restaurantName: restaurantInfo.restaurantName,
          restaurantNameEn: restaurantInfo.restaurantNameEn,
          restaurantNameJa: restaurantInfo.restaurantNameJa,
          restaurantNameZhCn: restaurantInfo.restaurantNameZhCn,
          restaurantNameZhTw: restaurantInfo.restaurantNameZhTw,
          restaurantNameKo: restaurantInfo.restaurantNameKo,
          items: [item],
        };
      }
      
      //         
      if (state.restaurantId !== restaurantId) {
        return state;
      }
      
      //     
      const existingItemIndex = state.items.findIndex(
        existingItem => existingItem.id === item.id
      );
      
      if (existingItemIndex >= 0) {
        //     
        const updatedItems = [...state.items];
        updatedItems[existingItemIndex] = {
          ...updatedItems[existingItemIndex],
          quantity: updatedItems[existingItemIndex].quantity + item.quantity,
        };
        
        return {
          ...state,
          items: updatedItems,
        };
      }
      
      //   
      return {
        ...state,
        items: [...state.items, item],
      };
    }
    
    case 'REPLACE_CART': {
      const { restaurantId, restaurantInfo, item } = action.payload;
      
      console.log(`Replacing cart with new restaurant: ${restaurantId}, ${restaurantInfo.restaurantName}`);
      
      //    ,     
      return {
        id: `cart-${Date.now()}`, //  ID 
        restaurantId,
        restaurantName: restaurantInfo.restaurantName,
        restaurantNameEn: restaurantInfo.restaurantNameEn,
        restaurantNameJa: restaurantInfo.restaurantNameJa,
        restaurantNameZhCn: restaurantInfo.restaurantNameZhCn,
        restaurantNameZhTw: restaurantInfo.restaurantNameZhTw,
        restaurantNameKo: restaurantInfo.restaurantNameKo,
        items: [item],
      };
    }
    
    case 'UPDATE_ITEM_QUANTITY': {
      if (!state) return null;
      
      const { itemId, quantity } = action.payload;
      
      if (quantity <= 0) {
        //  0   
        const updatedItems = state.items.filter(item => item.id !== itemId);
        
        //    null 
        if (updatedItems.length === 0) {
          return null;
        }
        
        return {
          ...state,
          items: updatedItems,
        };
      }
      
      //  
      const updatedItems = state.items.map(item => 
        item.id === itemId
          ? { ...item, quantity }
          : item
      );
      
      return {
        ...state,
        items: updatedItems,
      };
    }
    
    case 'REMOVE_ITEM': {
      if (!state) return null;
      
      const { itemId } = action.payload;
      
      const updatedItems = state.items.filter(item => item.id !== itemId);
      
      //    null 
      if (updatedItems.length === 0) {
        return null;
      }
      
      return {
        ...state,
        items: updatedItems,
      };
    }
    
    case 'CLEAR_CART':
      return null;
      
    case 'UPDATE_PRICES': {
      //      
      if (!state || !state.items.length) return state;
      
      //    state   
      // cart       
      //   getTotalPrice     
      return { ...state }; //       
    }
    
    case 'SET_ORDER_ID': {
      if (!state) return null;
      
      return {
        ...state,
        orderId: action.payload.orderId
      };
    }
      
    default:
      return state;
  }
};

//   
const CartContext = createContext<CartContextType | undefined>(undefined);

//   
export const CartProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [cart, dispatch] = useReducer(cartReducer, null, loadCartFromStorage);
  const [switchMessage, setSwitchMessage] = useState<string | null>(null);
  const [currentRestaurantId, setCurrentRestaurantId] = useState<string | null>(null);
  const [showCartReplaceConfirm, setShowCartReplaceConfirm] = useState<boolean>(false);
  const [pendingCartItem, setPendingCartItem] = useState<{ restaurantId: string; restaurantInfo: RestaurantInfo; item: CartItem } | null>(null);
  const [canceledItemId, setCanceledItemId] = useState<string | null>(null);
  const [restaurantMinOrderAmount, _setRestaurantMinOrderAmount] = useState<MinOrderAmount | null>(null);
  const [currentLanguage, setCurrentLanguage] = useState<SupportedLanguage>(getCurrentLanguage());
  
  //        
  const setRestaurantMinOrderAmount = useCallback((amount: number | null, currency?: SupportedCurrency) => {
    //  null    
    if (amount === null) {
      console.log(`[setRestaurantMinOrderAmount]    `);
      _setRestaurantMinOrderAmount(null);
      return;
    }
    
    //        
    const useCurrency = currency || getCurrencyFromLanguage(currentLanguage);
    
    //    
    _setRestaurantMinOrderAmount(prev => {
      if (prev) {
        const { amount: prevAmount, currency: prevCurrency } = prev;
        //     
        if (prevAmount === amount && prevCurrency === useCurrency) {
          console.log(`[setRestaurantMinOrderAmount]    : ${amount} ${useCurrency}`);
          return prev;
        }
        console.log(`[setRestaurantMinOrderAmount] : ${prevAmount} ${prevCurrency} -> ${amount} ${useCurrency}`);
      } else {
        console.log(`[setRestaurantMinOrderAmount]  : ${amount} ${useCurrency}`);
      }
      
      return { amount, currency: useCurrency };
    });
  }, [currentLanguage]);
  
  //         
  const updateCartPricesForLanguage = useCallback((language: SupportedLanguage) => {
    //        
    if (language === currentLanguage) {
      console.log(`[updateCartPricesForLanguage]  ${language}     `);
      return;
    }
    
    console.log(`[updateCartPricesForLanguage]  : ${currentLanguage} -> ${language}`);
    
    //        
    dispatch({
      type: 'UPDATE_PRICES',
      payload: { language }
    });
    
    //      
    setCurrentLanguage(language);
  }, [currentLanguage, dispatch]);
  
  //           ID
  useEffect(() => {
    //       ID 
    if (cart) {
      setCurrentRestaurantId(String(cart.restaurantId)); //   
    } else {
      setCurrentRestaurantId(null);
    }
  }, [cart]);
  
  //       
  useEffect(() => {
    if (cart) {
      localStorage.setItem('cart', JSON.stringify(cart));
    } else {
      localStorage.removeItem('cart');
      //      
      setCurrentRestaurantId(null);
      setRestaurantMinOrderAmount(null);
    }
  }, [cart, setRestaurantMinOrderAmount]); // setRestaurantMinOrderAmount 
  
  //      
  useEffect(() => {
    const handleLanguageChange = (event: Event) => {
      const customEvent = event as CustomEvent;
      if (customEvent.detail && customEvent.detail.language) {
        const newLanguage = customEvent.detail.language as SupportedLanguage;
        setCurrentLanguage(newLanguage);
        
        //    ( )
        updateCartPricesForLanguage(newLanguage);
      }
    };
    
    //     
    window.addEventListener(LANGUAGE_CHANGE_EVENT, handleLanguageChange);
    
    //      
    return () => {
      window.removeEventListener(LANGUAGE_CHANGE_EVENT, handleLanguageChange);
    };
  }, [updateCartPricesForLanguage]); // updateCartPricesForLanguage   
  
  //   
  const addToCart = (restaurantId: string, restaurantInfo: RestaurantInfo, item: CartItem) => {
    // restaurantInfo  
    if (!restaurantInfo || typeof restaurantInfo.restaurantName !== 'string') {
      console.error('Invalid restaurantInfo provided:', restaurantInfo);
      //  
      restaurantInfo = {
        restaurantName: 'Unknown Restaurant',
        restaurantNameEn: 'Unknown Restaurant'
      };
    } else {
      //      
      restaurantInfo = {
        ...restaurantInfo,
        restaurantName: restaurantInfo.restaurantName || 'Unknown Restaurant',
        restaurantNameEn: restaurantInfo.restaurantNameEn || restaurantInfo.restaurantName || 'Unknown Restaurant',
        restaurantNameJa: restaurantInfo.restaurantNameJa || restaurantInfo.restaurantNameEn || restaurantInfo.restaurantName,
        restaurantNameZhCn: restaurantInfo.restaurantNameZhCn || restaurantInfo.restaurantNameEn || restaurantInfo.restaurantName,
        restaurantNameZhTw: restaurantInfo.restaurantNameZhTw || restaurantInfo.restaurantNameEn || restaurantInfo.restaurantName,
        restaurantNameKo: restaurantInfo.restaurantNameKo || restaurantInfo.restaurantNameEn || restaurantInfo.restaurantName
      };
    }
    
    console.log(`Adding item to cart: ${restaurantId}, ${restaurantInfo.restaurantName}, ${item.name}`);
    
    //   ,    
    if (cart === null || (cart && cart.restaurantId === restaurantId)) {
      dispatch({
        type: 'ADD_ITEM',
        payload: { restaurantId, restaurantInfo, item },
      });
    } else {
      //      -   
      setPendingCartItem({ restaurantId, restaurantInfo, item });
      setShowCartReplaceConfirm(true);
    }
  };
  
  //   
  const confirmReplaceCart = () => {
    if (pendingCartItem) {
      const { restaurantId, restaurantInfo, item } = pendingCartItem;
      dispatch({
        type: 'REPLACE_CART',
        payload: { restaurantId, restaurantInfo, item },
      });
      
      //     
      setSwitchMessage(`Switched to ${restaurantInfo.restaurantName}. Your previous cart has been replaced.`);
      
      //  
      setShowCartReplaceConfirm(false);
      setPendingCartItem(null);
    }
  };
  
  //   
  const cancelReplaceCart = () => {
    //   ID 
    if (pendingCartItem) {
      setCanceledItemId(pendingCartItem.item.id);

      //     ID  (   )
      setTimeout(() => {
        setCanceledItemId(null);
      }, 100);
    }
    
    setShowCartReplaceConfirm(false);
    setPendingCartItem(null);
  };
  
  //   
  const clearCanceledItem = () => {
    setCanceledItemId(null);
  };
  
  //   
  const clearSwitchMessage = () => {
    setSwitchMessage(null);
  };
  
  //   
  const updateItemQuantity = (itemId: string, quantity: number) => {
    if (quantity <= 0) {
      //  0    
      removeItem(itemId);
      return;
    }
    
    dispatch({
      type: 'UPDATE_ITEM_QUANTITY',
      payload: { itemId, quantity },
    });
  };
  
  //  
  const removeItem = (itemId: string) => {
    //       ID  
    // cart   
    if (cart && cart.items.length === 1) {
      setCurrentRestaurantId(null);
    }
    
    dispatch({
      type: 'REMOVE_ITEM',
      payload: { itemId },
    });
  };
  
  //  
  const clearCart = () => {
    setSwitchMessage(null);
    setCurrentRestaurantId(null); //      ID 
    setRestaurantMinOrderAmount(null); //       
    dispatch({ type: 'CLEAR_CART' });
  };
  
  //   
  const getTotalPrice = (): number => {
    if (!cart) return 0;
    
    //     
    const currency = getCurrencyFromLanguage(currentLanguage);
    const priceField = getPriceFieldByCurrency(currency);
    
    return cart.items.reduce((total, item) => {
      //     
      let itemPrice: number;
      
      //       
      if (item[priceField as keyof CartItem] !== undefined) {
        //    
        itemPrice = item[priceField as keyof CartItem] as number;
      } else {
        //    
        itemPrice = item.price;
      }
      
      const itemTotal = itemPrice * item.quantity;
      
      //    (     )
      const optionsTotal = item.options.reduce((optTotal, option) => {
        const optionItemsTotal = option.optionItems.reduce(
          (itemTotal, optItem) => itemTotal + optItem.price,
          0
        );
        return optTotal + option.price + optionItemsTotal;
      }, 0) * item.quantity;
      
      return total + itemTotal + optionsTotal;
    }, 0);
  };
  
  //    
  const getTotalItems = (): number => {
    if (!cart) return 0;
    
    return cart.items.reduce((total, item) => total + item.quantity, 0);
  };
  
  //     (    )
  const getMinOrderAmount = (): number | null => {
    if (restaurantMinOrderAmount === null) {
      console.log(`[getMinOrderAmount]    (null)`);
      return null;
    }
    
    //     
    const targetCurrency = getCurrencyFromLanguage(currentLanguage);
    const { amount, currency: sourceCurrency } = restaurantMinOrderAmount;
    
    console.log(`[getMinOrderAmount]  : ${amount} ${sourceCurrency},  : ${targetCurrency}`);
    
    //      
    if (sourceCurrency === targetCurrency) {
      console.log(`[getMinOrderAmount]     : ${amount} ${targetCurrency}`);
      return amount;
    }
    
    //   
    const convertedAmount = convertCurrency(amount, sourceCurrency, targetCurrency);
    console.log(`[getMinOrderAmount]  : ${amount} ${sourceCurrency} -> ${convertedAmount} ${targetCurrency}`);
    return convertedAmount;
  };
  
  //    
  const isDeliveryAvailable = (minOrderAmount: number | null): boolean => {
    if (minOrderAmount === null) {
      console.log(`[isDeliveryAvailable]  null.  : true`);
      return true; //      
    }
    
    //    epsilon   
    const currency = getCurrencyFromLanguage(currentLanguage);
    let epsilon = 1; //  1 (KRW)
    
    switch(currency) {
      case 'USD':
        epsilon = 0.01; // USD   
        break;
      case 'JPY':
      case 'KRW':
        epsilon = 1; //     
        break;
      case 'CNY':
        epsilon = 0.1; //  0.1  
        break;
      case 'TWD':
        epsilon = 0.5; //   0.5  
        break;
    }
    
    const totalPrice = getTotalPrice();
    
    //    1%    (  )
    const isWithinOnePercent = (minOrderAmount - totalPrice) < (minOrderAmount * 0.01);
    const result = totalPrice >= minOrderAmount || isWithinOnePercent;
    
    //    
    console.log(
      `[isDeliveryAvailable]   : ${result}, =${totalPrice}, =${minOrderAmount}, =${currency}, 1%  =${isWithinOnePercent}`
    );
    
    return result;
  };
  
  //      
  const getAmountToMinOrder = (minOrderAmount: number | null): number => {
    if (minOrderAmount === null) return 0;
    
    const totalPrice = getTotalPrice();
    
    //    1%      
    const isWithinOnePercent = (minOrderAmount - totalPrice) < (minOrderAmount * 0.01);
    
    //        
    if (totalPrice >= minOrderAmount || isWithinOnePercent) {
      return 0;
    }
    
    return minOrderAmount - totalPrice;
  };
  
  //   ID 
  const fetchOrderId = async (addressId: string): Promise<string | null> => {
    if (!cart) return null;
    
    try {
      //  orderId   
      if (cart.orderId) {
        return cart.orderId;
      }
      
      //   
      const language = getCurrentLanguage();
      
      // API   
      const cartData = transformCartToRequest(cart, addressId, language.toUpperCase());
      
      //   API 
      const response = await createCart(cartData);
      
      //  orderId 
      if (response && response.orderId) {
        //  
        dispatch({ type: 'SET_ORDER_ID', payload: { orderId: response.orderId } });
        
        //   
        if (cart) {
          const updatedCart = { ...cart, orderId: response.orderId };
          localStorage.setItem('cart', JSON.stringify(updatedCart));
        }
        
        return response.orderId;
      }
      
      // orderId    
      if (response && response.id) {
        const cartResponse = await getCart(response.id);
        if (cartResponse && cartResponse.orderId) {
          //  
          dispatch({ type: 'SET_ORDER_ID', payload: { orderId: cartResponse.orderId } });
          
          //   
          if (cart) {
            const updatedCart = { ...cart, orderId: cartResponse.orderId };
            localStorage.setItem('cart', JSON.stringify(updatedCart));
          }
          
          return cartResponse.orderId;
        }
      }
      
      return null;
    } catch (error) {
      console.error('Error fetching order ID:', error);
      return null;
    }
  };
  
  //  
  const contextValue: CartContextType = {
    cart,
    currentRestaurantId,
    setCurrentRestaurantId,
    showCartReplaceConfirm,
    pendingCartItem,
    canceledItemId,
    restaurantMinOrderAmount,
    setRestaurantMinOrderAmount,
    addToCart,
    updateItemQuantity,
    removeItem,
    clearCart,
    getTotalPrice,
    getTotalItems,
    getMinOrderAmount,
    isDeliveryAvailable,
    getAmountToMinOrder,
    switchMessage,
    clearSwitchMessage,
    confirmReplaceCart,
    cancelReplaceCart,
    clearCanceledItem,
    updateCartPricesForLanguage,
    fetchOrderId
  };
  
  return (
    <CartContext.Provider value={contextValue}>
      {children}
    </CartContext.Provider>
  );
};

//     
export const useCart = (): CartContextType => {
  const context = useContext(CartContext);
  if (!context) {
    throw new Error('useCart must be used within a CartProvider');
  }
  return context;
}; 