import React, { useMemo, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useRestaurant } from '../hooks/useRestaurant';
import { useRestaurantMenus } from '../hooks/useRestaurantMenus';
import { useCart } from '../contexts/CartContext';
import BackHeader from '../components/BackHeader';
import RestaurantItem from '../components/RestaurantItem';
import NavigationBar from '../components/NavigationBar';
import MenuSection from '../components/MenuSection';
import CartBottomSheet from '../components/CartBottomSheet';
import LoadingState from '../components/LoadingState';
import ErrorState from '../components/ErrorState';
import EmptyState from '../components/EmptyState';
import { getStatusMessages } from '../config/constants';
import useTranslation from '../hooks/useTranslation';
import { SupportedCurrency, getCurrencyFromLanguage, getPriceFieldByCurrency } from '../utils/currencyUtils';
import { getCurrentLanguage, SupportedLanguage } from '../config/languageConfig';
import { Restaurant } from '../types/restaurant';

const RestaurantDetail: React.FC = () => {
  const { restaurantId = '' } = useParams<{ restaurantId: string }>();
  const navigate = useNavigate();
  const { setRestaurantMinOrderAmount } = useCart();
  const { t } = useTranslation();
  
  //     
  const currentLang = getCurrentLanguage();
  const selectedCurrency = getCurrencyFromLanguage(currentLang);
  
  //    
  const STATUS_MESSAGES = getStatusMessages();
  
  //   
  const { restaurant, loading: restaurantLoading, error: restaurantError } = useRestaurant(restaurantId);
  
  //   
  const { menuSections, loading: menuLoading, error: menuError, refetch: refetchMenus } = useRestaurantMenus(restaurantId);
  
  //    -        
  const minOrderAmount = useMemo(() => {
    if (!restaurant) return null;
    
    //     
    const currency = getCurrencyFromLanguage(currentLang);
    
    //      (: minOrderAmountKrw, minOrderAmountUsd )
    const currencyCode = currency.charAt(0) + currency.slice(1).toLowerCase();
    const minOrderAmountField = `minOrderAmount${currencyCode}`;
    
    //      ,     
    return restaurant[minOrderAmountField as keyof typeof restaurant] as number || restaurant.minOrderAmount;
  }, [restaurant, currentLang]);
  
  // CartContext    
  useEffect(() => {
    if (minOrderAmount !== null) {
      //       
      console.log(`[RestaurantDetail]    : ${minOrderAmount} ${selectedCurrency}`);
      setRestaurantMinOrderAmount(minOrderAmount, selectedCurrency);
    }
  }, [minOrderAmount, selectedCurrency, setRestaurantMinOrderAmount]);
  
  //      
  const displayName = useMemo(() => {
    if (restaurantLoading || !restaurant) return '[Shop Name]';
    
    //     
    switch(currentLang) {
      case 'ko':
        if (restaurant.nameKo) return restaurant.nameKo;
        break;
      case 'ja':
        if (restaurant.nameJa) return restaurant.nameJa;
        break;
      case 'zh-CN':
        if (restaurant.nameZhCn) return restaurant.nameZhCn;
        break;
      case 'zh-TW':
        if (restaurant.nameZhTw) return restaurant.nameZhTw;
        break;
      // 'en'  fallback  
    }
    
    //    ,       
    if (restaurant.nameEn) {
      if (currentLang !== 'en') {
        return `${restaurant.nameEn}`;
      }
      return restaurant.nameEn;
    }
    
    //     
    return restaurant.name;
  }, [restaurant, restaurantLoading, currentLang]);
  
  //   
  const handleCheckout = () => {
    console.log('RestaurantDetail: handleCheckout called, navigating to /cart');
    
    // React Router navigate 
    navigate('/cart', { replace: false });
    
    // :  window.location  (100ms   )
    setTimeout(() => {
      //        
      if (window.location.pathname.indexOf('/cart') === -1) {
        console.log('Navigation fallback: using window.location');
        window.location.href = '/cart';
      }
    }, 100);
  };
  
  // restaurantId  
  if (!restaurantId) {
    return (
      <div className="min-h-screen bg-gray-50">
        <BackHeader title={t('error.title')} />
        <div className="p-4">
          <ErrorState message={t('restaurant.invalidId')} onRetry={() => window.location.reload()} />
        </div>
        <NavigationBar />
      </div>
    );
  }
  
  //   
  if (restaurantLoading) {
    return (
      <div className="min-h-screen bg-gray-50">
        <BackHeader title={displayName} />
        <div className="p-4">
          <LoadingState 
            message={STATUS_MESSAGES.loading.restaurants} 
            id={`restaurant-detail-${restaurantId}`}
          />
        </div>
        <NavigationBar />
      </div>
    );
  }
  
  //  
  if (restaurantError) {
    return (
      <div className="min-h-screen bg-gray-50">
        <BackHeader title={t('error.title')} />
        <div className="p-4">
          <ErrorState message={restaurantError} onRetry={() => window.location.reload()} />
        </div>
        <NavigationBar />
      </div>
    );
  }
  
  //    
  if (!restaurant) {
    return (
      <div className="min-h-screen bg-gray-50">
        <BackHeader title={t('error.notFound')} />
        <div className="p-4">
          <EmptyState message={t('restaurant.notFound')} />
        </div>
        <NavigationBar />
      </div>
    );
  }
  
  return (
    <div className="min-h-screen bg-gray-50">
      {/* GNB */}
      <BackHeader title={displayName} />
      
      <main className="p-4 pb-24">
        {/*    */}
        <div className="mb-4">
          <RestaurantItem 
            restaurant={restaurant} 
            disableNavigation={true} 
            currency={selectedCurrency}
          />
        </div>
        
        {/*  */}
        <div className="border-b border-gray-200 my-4"></div>
        
        {/*   */}
        {menuLoading ? (
          <LoadingState 
            message={STATUS_MESSAGES.loading.menus} 
            id={`restaurant-menus-${restaurantId}`}
          />
        ) : menuError ? (
          <ErrorState message={menuError} onRetry={refetchMenus} />
        ) : menuSections.length === 0 ? (
          <EmptyState 
            message={STATUS_MESSAGES.empty.menus} 
            subtitle={t('restaurant.noMenuItems')}
            actionText={t('action.refresh')}
            onAction={refetchMenus}
            hideIcon={true}
          />
        ) : (
          <div className="space-y-2">
            {menuSections.map(section => (
              <MenuSection 
                key={section.id} 
                section={section} 
                restaurantId={restaurantId}
                restaurantName={displayName}
                restaurantMultiNames={{
                  name: restaurant.name,
                  nameEn: restaurant.nameEn || restaurant.name,
                  nameKo: restaurant.nameKo || restaurant.nameEn || restaurant.name,
                  nameJa: restaurant.nameJa || restaurant.nameEn || restaurant.name,
                  nameZhCn: restaurant.nameZhCn || restaurant.nameEn || restaurant.name,
                  nameZhTw: restaurant.nameZhTw || restaurant.nameEn || restaurant.name
                }}
                currency={selectedCurrency}
              />
            ))}
            {/*       */}
            <div className="h-20"></div>
          </div>
        )}
      </main>
      
      {/*   */}
      <CartBottomSheet 
        minOrderAmount={minOrderAmount}
        onCheckout={handleCheckout}
        restaurantId={restaurantId}
        currency={selectedCurrency}
      />
      
      {/*    */}
      <NavigationBar />
    </div>
  );
};

export default RestaurantDetail; 