import React, { useState, useEffect } from 'react';
import { useCart } from '../contexts/CartContext';
import { formatPrice as formatPriceWithCurrency, SupportedCurrency, getCurrencyFromLanguage } from '../utils/currencyUtils';
import { getCurrentLanguage } from '../config/languageConfig';
import { Link } from 'react-router-dom';
import useTranslation from '../hooks/useTranslation';

interface CartBottomSheetProps {
  minOrderAmount: number | null;
  onCheckout: () => void;
  restaurantId: string;
  currency?: SupportedCurrency;
}

const CartBottomSheet: React.FC<CartBottomSheetProps> = ({ 
  minOrderAmount, 
  onCheckout, 
  restaurantId,
  currency
}) => {
  const { 
    cart, 
    getTotalPrice, 
    getTotalItems, 
    isDeliveryAvailable, 
    getAmountToMinOrder,
    switchMessage,
    clearSwitchMessage,
    setCurrentRestaurantId,
    currentRestaurantId
  } = useCart();
  const { t } = useTranslation();
  const [isVisible, setIsVisible] = useState(false);
  const [showSwitchMessage, setShowSwitchMessage] = useState(false);
  
  //     
  const currentLang = getCurrentLanguage();
  const effectiveCurrency = currency || getCurrencyFromLanguage(currentLang);
  
  //   ID 
  useEffect(() => {
    //     
    if (currentRestaurantId !== restaurantId) {
      setCurrentRestaurantId(restaurantId);
    }
  }, [restaurantId, setCurrentRestaurantId, currentRestaurantId]);
  
  //         
  useEffect(() => {
    if (cart && cart.items.length > 0 && cart.restaurantId === restaurantId) {
      const timer = setTimeout(() => {
        setIsVisible(true);
      }, 50);
      return () => clearTimeout(timer);
    } else {
      setIsVisible(false);
    }
  }, [cart, restaurantId]);
  
  //    
  useEffect(() => {
    if (switchMessage) {
      setShowSwitchMessage(true);
      const timer = setTimeout(() => {
        setShowSwitchMessage(false);
        clearSwitchMessage();
      }, 3000); // 3   
      return () => clearTimeout(timer);
    }
  }, [switchMessage, clearSwitchMessage]);
  
  //  ,  ,       
  if (
    ((!cart || cart.items.length === 0) && !showSwitchMessage) || 
    (cart && cart.restaurantId !== restaurantId)
  ) {
    return null;
  }
  
  const totalPrice = getTotalPrice();
  const totalItems = getTotalItems();
  const deliveryAvailable = isDeliveryAvailable(minOrderAmount);
  const amountToMinOrder = getAmountToMinOrder(minOrderAmount);
  
  //   (  )
  const formatPrice = (price: number) => {
    return formatPriceWithCurrency(price, effectiveCurrency);
  };
  
  return (
    <div 
      className={`fixed bottom-[60px] left-0 right-0 bg-white z-10 rounded-t-xl transition-all duration-300 ease-in-out ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-4'}`}
      style={{ 
        boxShadow: '0 -4px 6px -1px rgba(0, 0, 0, 0.1), 0 -2px 4px -1px rgba(0, 0, 0, 0.06)'
      }}
    >
      {/*    */}
      {showSwitchMessage && switchMessage && (
        <div className="bg-yellow-100 text-yellow-700 px-4 py-2 border-b border-yellow-200">
          <div className="flex justify-between items-center">
            <p className="text-sm font-medium">{switchMessage}</p>
            <button 
              onClick={() => {
                setShowSwitchMessage(false);
                clearSwitchMessage();
              }}
              className="text-yellow-500 hover:text-yellow-700"
            >
              <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
              </svg>
            </button>
          </div>
        </div>
      )}
      
      {/*   -        */}
      {cart && cart.items.length > 0 && cart.restaurantId === restaurantId && (
        <div className="py-2.5 px-4">
          <div className="flex items-center justify-between gap-4">
            <div className="flex-shrink-0 w-2/5">
              {deliveryAvailable ? (
                <span className="text-base font-medium text-gray-800 whitespace-nowrap">{t('cart.deliveryAvailable')}</span>
              ) : (
                <span className="text-base font-medium text-red-500 whitespace-nowrap">{t('cart.addMoreAmount', { amount: formatPrice(amountToMinOrder) })}</span>
              )}
            </div>
            
            <div className="flex-grow flex justify-end w-3/5">
              <Link 
                to="/cart"
                className={`max-w-[200px] ml-auto text-white py-3 px-3 rounded-lg font-semibold flex items-center justify-center z-50 ${
                  deliveryAvailable ? 'bg-red-500 hover:bg-red-600 active:bg-red-700' : 'bg-gray-300 opacity-70'
                }`}
                style={{ 
                  position: 'relative', 
                  pointerEvents: deliveryAvailable ? 'auto' : 'none',
                  textDecoration: 'none'
                }}
              >
                <div className={`rounded-full w-5 h-5 flex items-center justify-center mr-2 ${
                  deliveryAvailable ? 'bg-white text-red-500' : 'bg-white text-gray-400'
                }`}>
                  <span className="text-sm font-bold">{totalItems}</span>
                </div>
                <span className="text-lg font-semibold text-white">{formatPrice(totalPrice)}</span>
              </Link>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default CartBottomSheet; 